<template>
  <view class="call-view">
    <!-- 本地视频画面 -->
    <grtc-video v-if="call.mediaType === 1" :userId="currentUser.id" class="main-video"></grtc-video>

    <cover-view class="profile">
      <image class="user-avatar" mode="widthFix" :src="call.caller.data.avatar"></image>
      <text class="user-name">{{call.caller.data.name}}</text>
    </cover-view>
    <view class="status-box">
      <text class="status-notice">邀请你进行通话</text>
      <image src="https://wangzhenghai-oss.oss-cn-hangzhou.aliyuncs.com/04dfd54b-f131-49f6-aee9-1ef8f988a8cd.gif" class="loading"></image>
    </view>

    <view class="action-box">
      <view class="action-item" @click="end()">
        <view class="end-background">
          <image class="action-icon" src="https://wangzhenghai-oss.oss-cn-hangzhou.aliyuncs.com/8af91edd-40d2-4bbb-8a5c-caef7457bb07.png"></image>
        </view>
        <text class="action-text">拒绝</text>
      </view>
      <view class="action-item" @click="accept()">
        <view class="accept-background">
          <image class="action-icon rotate" src="https://wangzhenghai-oss.oss-cn-hangzhou.aliyuncs.com/8af91edd-40d2-4bbb-8a5c-caef7457bb07.png"></image>
        </view>
        <text class="action-text">接听</text>
      </view>
    </view>

  </view>
</template>

<script>
  const GRTC = uni.$GRTC;
  export default {
    data() {
      return {
        call: GRTC.currentCall(),
        currentUser: uni.$currentUser
      }
    },
    onLoad() {
      this.initListener();
    },
    onUnload() {
      GRTC.off(GRTC.EVENT.USER_QUIT, this.onUserQuit);
    },
    onBackPress(event) {
      return event.from === 'backbutton';  // 禁止安卓侧滑返回
    },
    methods: {
      initListener() {
        GRTC.on(GRTC.EVENT.USER_QUIT, this.onUserQuit);
      },
      onUserQuit(event) {
        const username = event.user.id === this.currentUser.id ? '' : event.user.data.name;
        let message = '';
        switch (event.reason) {
          case 'RING_TIMEOUT':
            message = `超时未接听`;
            break;
          case 'GOEASY_DISCONNECTED':
            message = `${username}网络异常`;
            break;
          case 'HANDLED_ON_ANOTHER_DEVICE':
            message = `其他设备已处理`;
            break;
          case 'CANCELLED':
            message = `${username}已取消`;
            break;
          case 'REJECTED':
            message = `已拒绝`;
            break;
        }
        plus.nativeUI.toast(message);
        uni.navigateBack();
      },
      accept() {
        GRTC.accept().then(() => {
          uni.redirectTo({
            url: './call'
          });
        }).catch((e) => {
          console.log('e', e);
        })
      },
      end() {
        GRTC.end();
      },
    }
  }
</script>

<style scoped>
@import url('/static/style/rtcCallStyle.css');
</style>
